	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Engineer Genes","date":"30 Mar 2004","slug":"/2004-03-30-engineer-genes/","tags":"Y2004"},"html":"<p><img alt=\"crh_bridge.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/03/crh_bridge.jpg\" width=\"320\" height=\"240\" border=\"0\" /></p>\n<p>He has them from Grandpa.</p>"},"next":{"fields":{"title":"Posts not intended for my mother","date":"29 Mar 2004","slug":"/2004-03-29-posts-not-intended-for-my-mother/"}},"previous":{"fields":{"title":"Homework","date":"30 Mar 2004","slug":"/2004-03-30-homework/"}}},{"node":{"fields":{"title":"Posts not intended for my mother","date":"29 Mar 2004","slug":"/2004-03-29-posts-not-intended-for-my-mother/","tags":"Y2004"},"html":"<p>My blog is schizophrenic, just like me. I sometimes blog about my family, sometimes about politics, sometimes about being an expat in a strange land, and sometimes about geeky computer stuff. Since the geeky computer stuff doesn't have much to do with the other stuff, and my mother doesn't really want to read the geeky stuff, I'm going to try putting it on a separate page called <a title=\"Posts not intended for my mother\" href=\"http://www.papascott.de/tech.php\">PapaTech</a>.    We'll see how that works.</p>"},"next":{"fields":{"title":"Firesomething","date":"29 Mar 2004","slug":"/2004-03-29-firesomething/"}},"previous":{"fields":{"title":"Engineer Genes","date":"30 Mar 2004","slug":"/2004-03-30-engineer-genes/"}}},{"node":{"fields":{"title":"Firesomething","date":"29 Mar 2004","slug":"/2004-03-29-firesomething/","tags":"Y2004"},"html":"<p>As seen at <a href=\"http://slashdot.org/article.pl?sid=04/03/27/0250250&amp;mode=thread&amp;tid=126&amp;tid=154&amp;tid=95\" title=\"Slashdot | Firefox Extension Lets You Pick the Name\">slashdot</a>, <a href=\"http://www.cosmicat.com/software/firesomething/\">firesomething</a> is an extension for <s>firebird</s>, er, <a href=\"http://www.mozilla.org/products/firefox/\">firefox</a> that will rename the browser at random, with different names for each window. Best of all, you can put the random name into your User Agent, so you can mess with the minds of webmasters who spend too much time reading their logs. Right now I am browsing with Mooncow and posting with Waterworm. Just so you know.</p>"},"next":{"fields":{"title":"Haberler","date":"28 Mar 2004","slug":"/2004-03-28-haberler/"}},"previous":{"fields":{"title":"Posts not intended for my mother","date":"29 Mar 2004","slug":"/2004-03-29-posts-not-intended-for-my-mother/"}}}],"pathPrefix":"","first":false,"last":false,"index":504,"pageCount":1101}}

